import React, { Component } from 'react';
import {Container, Row, Col } from 'reactstrap';
import Quote from './Quote';
import './Home.css'

class Home extends Component {
  render() {
    return (
        <div id="home">
            <img src = "/images/home_pic.jpg" id = "selfie" alt="me"/>
            <Quote />

            <Container id="react_strap">
              <Row>
                <Col xs="12" className="center">
                  <h1>Welcome to My Website</h1>
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">GOAL</Col>
                <Col xs="12" sm="9">
                  <p>A demonstration of my engineering capabilities and personal interests.
                     I use this website as a playground to continuously improve myself as a web engineer.
                  </p>
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">REACT</Col>
                <Col xs="12" sm="9">
                  <p>The DOM is rendered with <a href="https://reactjs.org/">React</a>. I chose React
                  because I found it closely aligned with my development of understanding JavaScript
                  standards from <a href="https://medium.com/techtrument/reactjs-as-an-ecosystem-8d1ae90731f2">ES5 and ES6</a>.</p> 
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">LINTER</Col>
                <Col xs="12" sm="9">
                  <p><a href="https://github.com/airbnb/javascript"> Airbnb's JavaScript ESLint</a> was used in writing clean code.
                  Using a Linter has helped remove bad habits and write code in a clean, consistent, and readable manner.</p>
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">DEPENDICIES</Col>
                <Col xs="12" sm="9">
                  <p><a href="https://www.npmjs.com/"> Node Package Manager</a> to manage dependencies. Besides being the default
                  package manager for JavaScript, I have found a variety of powerful external tools developed and publicy available
                  to help speed up development.</p>
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">GITHUB</Col>
                <Col xs="12" sm="9">
                  <p>Version control using <a href="https://github.com/">GitHub</a>. GitHub was useful in managing progress and monitoring
                  the various stages of development over many commits. GitHub is even more useful when collaborating with others, but for
                  this website it was developed solely by myself.</p>
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">RESPONSIVE</Col>
                <Col xs="12" sm="9">
                  <p><a href="https://getbootstrap.com/docs/4.0/layout/overview/">Bootstrap 4's </a> media queries
                     to account for various devices. More specifically, 1200px for extra large devices, 992px for large devices,
                     768px for medium devices, and 576px for smaller devices. If you're on desktop, go ahead and resize. 
                     Column format and text size will reflect window size.
                  </p>
                </Col>
              </Row>

              <Row>
                <Col xs="12" sm="3" className="left">CODE</Col>
                <Col xs="12" sm="9"><a href="https://github.com/MichaelGeng/michaelhgeng" target="_blank"><button className="parentButton">View Source Code <i className="fas fa-code hvr-grow-rotate"></i></button></a></Col>
              </Row>
            </Container>
        </div>
    );
  }
}

export default Home;


